"use client"
import React from 'react';
import { Box, Button, Container, Typography } from '@mui/material';
import { styled } from '@mui/system';
import Image from 'next/image'; // For Next.js projects

const profiles = [
  {
    name: 'Agent Pakulla',
    role: 'Marketing',
    image: '/images/agent-pakulla.jpg', // Replace with actual paths
  },
  {
    name: 'Nina Wallker',
    role: 'Accountant',
    image: '/images/nina-wallker.jpg',
  },
  {
    name: 'Tom Wilson',
    role: 'Marketing',
    image: '/images/tom-wilson.jpg',
  },
  {
    name: 'Samuel Smith',
    role: 'Accountant',
    image: '/images/samuel-smith.jpg',
  },
];

const SliderContainer = styled(Box)(({ theme }) => ({
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'space-between',
  width: '100%',
}));

const Slide = styled(Box)(({ theme }) => ({
  flex: '0 0 25%', // Display 4 slides at a time
  padding: theme.spacing(2),
  textAlign: 'center',
}));

const SliderButton = styled(Button)(({ theme }) => ({
  minWidth: 40,
  height: 40,
  borderRadius: '50%',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
}));

const OurAgents = () => {
  const [startIndex, setStartIndex] = React.useState(0);

  const handlePrev = () => {
    setStartIndex((prev) => (prev > 0 ? prev - 1 : profiles.length - 1));
  };

  const handleNext = () => {
    setStartIndex((prev) => (prev < profiles.length - 4 ? prev + 1 : 0));
  };

  const visibleProfiles = profiles.slice(startIndex, startIndex + 4);

  return (
    <Container>
        <Box textAlign="center" p={4}>
      <Typography variant="h5" mb={2}>
        Lorem ipsum dolor sit amet, consectetur adipiscing elit.
      </Typography>
      <SliderContainer>
        <SliderButton onClick={handlePrev}>{'<'}</SliderButton>
        {visibleProfiles.map((profile, index) => (
          <Slide key={index}>
            <Box
              sx={{
                borderRadius: 2,
                overflow: 'hidden',
                border: '1px solid #ccc',
                width: '100%',
                aspectRatio: '1 / 1.2', // Adjust for aspect ratio
              }}
            >
              <Image
                src={profile.image}
                alt={profile.name}
                layout="fill"
                objectFit="cover"
              />
            </Box>
            <Typography variant="h6" mt={2}>
              {profile.name}
            </Typography>
            <Typography variant="body2" color="textSecondary">
              {profile.role}
            </Typography>
          </Slide>
        ))}
        <SliderButton onClick={handleNext}>{'>'}</SliderButton>
      </SliderContainer>
    </Box>
    </Container>
  );
};

export default OurAgents;
